import React, { Component } from "react";

export default class OverlayNav extends Component {
    render() {
        return (
            <div id={this.props.id} className={`${this.props.className} text-3xl md:text-4xl xl:text-5xl darkgrey-t`}>
                {this.props.children}
            </div>
        );
    }
}
