import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ContentArea from "../../components/ContentArea";
import Nav from "../../components/Nav";
import ProjectPreview from "../../components/ProjectPreview";

export default class Work extends Component {
    constructor(props) {
        super(props);

        this.state = {
            projects: null
        };
    }

    componentDidMount() {
        if (this.props.projects != null) {
            this.setProjectList();
        }
    }

    componentDidUpdate() {
        if (this.state.projects == null && this.props.projects != null) {
            this.setProjectList();
        }
    }

    setProjectList() {
        this.setState({
            projects: this.props.projects.map((project, index) => {
                return <ProjectPreview project={project} index={index} key={index} />;
            })
        });
    }

    componentWillUnmount() {
        console.log("unmount");
    }

    render() {
        return (
            <div className="darkgrey">
                <ContentArea>
                    <Nav id="worknav" className="pt-12">
                        <div className="hidden lg:block">
                            <NavLink to="/" className="text-xl">
                                Home
                            </NavLink>
                            <NavLink to="/about" className="ml-6 text-xl">
                                About
                            </NavLink>
                            <NavLink to="/work" className="ml-6 text-xl">
                                Work
                            </NavLink>
                            <NavLink to="/contact" className="ml-6 text-xl">
                                Contact
                            </NavLink>
                        </div>
                        <div className="block lg:hidden">
                            <button id="openNav">
                                <i className="fas fa-bars text-2xl"></i>
                            </button>
                        </div>
                    </Nav>
                    <section id="work" className="w-full h-full flex flex-row flex-wrap py-24">
                        {this.state.projects}
                    </section>

                    <div className="lg:h-64 block"></div>
                </ContentArea>
            </div>
        );
    }
}
