import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ContentArea from "../../components/ContentArea";
import Nav from "../../components/Nav";
import Header from "../../components/Header";

export default class About extends Component {
    render() {
        return (
            <div className="color">
                <ContentArea>
                    <Nav id="aboutnav" className="pt-12 darkgrey-t">
                        <div className="hidden lg:block">
                            <NavLink to="/" className="text-xl">
                                Home
                            </NavLink>
                            <NavLink to="/about" className="ml-6 text-xl">
                                About
                            </NavLink>
                            <NavLink to="/work" className="ml-6 text-xl">
                                Work
                            </NavLink>
                            <NavLink to="/contact" className="ml-6 text-xl">
                                Contact
                            </NavLink>
                        </div>
                        <div className="block lg:hidden">
                            <button id="openNav">
                                <i className="fas fa-bars text-2xl"></i>
                            </button>
                        </div>
                    </Nav>
                    <section id="about" className="py-24 darkgrey-t">
                        <div className="flex flex-col xl:flex-row ">
                            <div className="flex">
                                <Header size="large" className="title">
                                    About
                                </Header>
                            </div>
                            <div className="text-base md:text-lg xl:text-xl">
                                <p className="text">I am a creative designer and developer who loves making ideas come to life. Currently live in Finland, where I have been working freelance for 3 years. Constantly developing new skills and applying them to my workflow.</p>
                            </div>
                        </div>
                        <div className="flex flex-col xl:flex-row ">
                            <div className="flex">
                                <Header size="large" className="title">
                                    Skills
                                </Header>
                            </div>
                            <div className="text-base md:text-lg xl:text-xl">
                                <p className="text">With multiple years of practice in creating custom designs for all kinds of purposes, I have learned to solve complex problems based on experience, research and user feedback.</p>
                                <p className="text">In addition to visual, web and interaction design I have also gathered some experience in front-end development using HTML5, CSS3, JS, React and PHP.</p>
                            </div>
                        </div>
                        <div className="flex flex-col xl:flex-row ">
                            <div className="flex">
                                <Header size="large" className="title">
                                    Education
                                </Header>
                            </div>
                            <div className="flex flex-col text-base md:text-lg xl:text-xl">
                                <div className="study">
                                    <p>2017-2019</p>
                                    <p>KEUDA, Degree in Information and communications technology</p>
                                    <p>Kerava, Finland</p>
                                </div>
                            </div>
                        </div>
                    </section>
                </ContentArea>
            </div>
        );
    }
}
