import React, { Component } from "react";
import { NavLink, Link } from "react-router-dom";
import ContentArea from "../../components/ContentArea";
import Nav from "../../components/Nav";
import Header from "../../components/Header";
import "./style.css";

export default class Index extends Component {
    render() {
        return (
            <div className="darkgrey">
                <ContentArea className="justify-between">
                    <Nav id="headernav" className="pt-12">
                        <div className="hidden lg:block">
                            <NavLink to="/" className="text-xl">
                                Home
                            </NavLink>
                            <NavLink to="/about" className="ml-6 text-xl">
                                About
                            </NavLink>
                            <NavLink to="/work" className="ml-6 text-xl">
                                Work
                            </NavLink>
                            <NavLink to="/contact" className="ml-6 text-xl">
                                Contact
                            </NavLink>
                        </div>
                        <div className="block lg:hidden">
                            <button id="openNav">
                                <i className="fas fa-bars text-2xl"></i>
                            </button>
                        </div>
                    </Nav>
                    <header className="py-16">
                        <Header size="medium" className="color-t pb-12">
                            Patrik Pessi
                        </Header>
                        <div className="almostwhite-t leading-tight pb-12">
                            <Header size="large">Designer, front-end developer.</Header>
                            <Header size="large" style={{ maxWidth: "39ch" }}>
                                Transforming simple ideas into creative masterpieces and beyond.
                            </Header>
                        </div>
                        <Link to="/about" className="lightgrey-t text-base md:text-lg xl:text-xl">
                            Learn more
                        </Link>
                    </header>
                    <div className="pb-12">
                        <div className="flex" id="social_links">
                            <a className="w-12 h-12 p-4 rounded-full flex justify-center align-center" target="blank" href="https://dribbble.com/Batti">
                                <i className="fab fa-dribbble dribbble-t text-xl"></i>
                            </a>
                            <a className="w-12 h-12 p-4 rounded-full flex justify-center align-center" target="blank" href="https://twitter.com/BPatti12">
                                <i className="fab fa-twitter twitter-t text-xl"></i>
                            </a>
                        </div>
                    </div>
                </ContentArea>
            </div>
        );
    }
}
