import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ContentArea from "../../components/ContentArea";
import Nav from "../../components/Nav";
import Header from "../../components/Header";
import Loader from "../../components/Loader";

export default class ProjectView extends Component {
    constructor(props) {
        super(props);

        this.state = {
            project: null,
            projectImages: null
        };
    }

    componentDidMount() {
        if (this.state.project == null && this.props.projects != null) {
        }
    }

    componentDidUpdate() {
        if (this.props.projects != null && this.state.project == null) {
            let thisProject = this.getProjectFromArray();
            let images = thisProject.images.map((image, key) => {
                let imagesrc = image.resolutions.desktop.url;
                return <img src={`${imagesrc}`} alt={`${imagesrc}`} key={key} className="w-full" />;
            });
            this.setState({ project: thisProject, projectImages: images });
        }
    }

    getProjectFromArray() {
        let thisProjectName = this.props.props.match.params.name.split("-").join(" ");
        let projects = this.props.projects;
        for (let i = 0; i < projects.length; i++) {
            let project = projects[i];
            if (project.name == thisProjectName) {
                return project;
            }
        }
        return null;
    }

    render() {
        return (
            <div className="darkgrey">
                <ContentArea>
                    <Nav id="projectviewnav" className="pt-12">
                        <div className="hidden lg:block">
                            <NavLink to="/" className="text-xl">
                                Home
                            </NavLink>
                            <NavLink to="/about" className="ml-6 text-xl">
                                About
                            </NavLink>
                            <NavLink to="/work" className="ml-6 text-xl">
                                Work
                            </NavLink>
                            <NavLink to="/contact" className="ml-6 text-xl">
                                Contact
                            </NavLink>
                        </div>
                        <div className="block lg:hidden">
                            <button id="openNav">
                                <i className="fas fa-bars text-2xl"></i>
                            </button>
                        </div>
                    </Nav>
                    <section id="projectview" className="py-24">
                        {this.state.projectImages ? (
                            <div className="w-full flex-col">
                                <div className="lightgrey-t mb-6">
                                    <NavLink to="/work">
                                        <i className="fas fa-arrow-left"></i> Back to work
                                    </NavLink>
                                </div>
                                <Header size="large" className="mb-6">{`${this.state.project ? this.state.project.displayName : ""}`}</Header>
                                <div className="text-base md:text-lg xl:text-xl pb-24">
                                    <p className="text">{`${this.state.project ? this.state.project.summary : ""}`}</p>
                                </div>
                                {this.state.projectImages}
                            </div>
                        ) : (
                            <Loader />
                        )}
                    </section>
                </ContentArea>
            </div>
        );
    }
}
