import React, { Component } from "react";

export default class ProjectPreview extends Component {
    constructor(props) {
        super(props);

        this.offset = props.index % 2 == 0 ? false : true;
    }

    getLink() {
        let projectlink = this.props.project.name.split(" ").join("-");
        return `/projects/${projectlink}`;
    }

    getImgLink() {
        return `${this.props.project.images[0].resolutions.desktop.url}`;
    }

    getDisplayName() {
        return `${this.props.project.displayName}`;
    }

    render() {
        return (
            <div className={`w-full lg:w-1/2 py-4 md:py-8 lg:p-8 ${this.offset ? "offset" : ""}`}>
                <div>
                    <img src={`${this.getImgLink()}`} alt={`${this.getImgLink()}`} />
                    <a href={`${this.getLink()}`} className="flex flex-col justify-center items-center absolute inset-0 text-xl md:text-2xl xl:text-3xl cardlink">{`${this.props.project.displayName}`}</a>
                </div>
            </div>
        );
    }
}
