import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ContentArea from "../../components/ContentArea";
import Nav from "../../components/Nav";

export default class Work extends Component {
    render() {
        return (
            <div className="darkgrey">
                <ContentArea>
                    <Nav id="contactnav" className="pt-12">
                        <div className="hidden lg:block">
                            <NavLink to="/" className="text-xl">
                                Home
                            </NavLink>
                            <NavLink to="/about" className="ml-6 text-xl">
                                About
                            </NavLink>
                            <NavLink to="/work" className="ml-6 text-xl">
                                Work
                            </NavLink>
                            <NavLink to="/contact" className="ml-6 text-xl">
                                Contact
                            </NavLink>
                        </div>
                        <div className="block lg:hidden">
                            <button id="openNav">
                                <i className="fas fa-bars text-2xl"></i>
                            </button>
                        </div>
                    </Nav>
                    <section id="contact" className="w-full h-full flex flex-col justify-center items-center">
                        <a className="text-2xl md:text-3xl xl:text-4xl" target="blank" href="mailto:contact@geometricarts.ga">
                            contact@geometricarts.ga
                        </a>
                        <a className="text-2xl md:text-3xl xl:text-4xl" target="blank" href="https://twitter.com/BPatti12">
                            <i className="fab fa-twitter twitter-t"></i> Twitter
                        </a>
                        <a className="text-2xl md:text-3xl xl:text-4xl" target="blank" href="https://dribbble.com/Batti">
                            <i className="fab fa-dribbble dribbble-t"></i> Dribbble
                        </a>
                    </section>
                </ContentArea>
            </div>
        );
    }
}
