import React, { Component } from "react";
import { BrowserRouter, Switch, Route, NavLink } from "react-router-dom";
import Index from "./pages/index/Index";
import About from "./pages/about/About";
import Work from "./pages/work/Work";
import Contact from "./pages/contact/Contact";
import Notfound from "./pages/notfound/404";
import OverlayNav from "./components/OverlayNav";
import getPortfolio from "../app/modules/portfolioData";
import ProjectView from "../app/pages/projectview/ProjectView";

export default class App extends Component {
    constructor() {
        super();
        this.state = { portfolioData: null };
    }

    componentDidMount() {
        getPortfolio().then(data => {
            this.setState({ portfolioData: data });
        });
    }

    render() {
        return (
            <BrowserRouter basename={process.env.PUBLIC_URL}>
                <Switch>
                    <Route exact path="/projects/:name" render={props => <ProjectView props={props} projects={this.state.portfolioData ? this.state.portfolioData.projects : null} />} />
                    <Route exact path="/contact">
                        <Contact />
                    </Route>
                    <Route exact path="/work">
                        <Work projects={this.state.portfolioData ? this.state.portfolioData.projects : null} />
                    </Route>
                    <Route exact path="/about">
                        <About />
                    </Route>
                    <Route exact path="/">
                        <Index />
                    </Route>
                    <Route>
                        <Notfound />
                    </Route>
                </Switch>
                <OverlayNav id="overlaynav" className="fixed inset-0 w-screen h-screen flex flex-col justify-center items-center color lg:hidden navup">
                    <button id="closeNav" className="absolute top-0 right-0 px-8 py-4">
                        <i className="fas fa-times text-2xl"></i>
                    </button>
                    <NavLink to="/">Home</NavLink>
                    <NavLink to="/about">About</NavLink>
                    <NavLink to="/work">Work</NavLink>
                    <NavLink to="/contact">Contact</NavLink>
                </OverlayNav>
            </BrowserRouter>
        );
    }
}
