import React, { Component } from "react";

export default class Header extends Component {
    constructor(props) {
        super(props);

        switch (props.size) {
            case "small":
                this.sizes = "text-base md:text-lg xl:text-xl";
                break;
            case "medium":
                this.sizes = "text-xl md:text-2xl xl:text-3xl";
                break;
            case "large":
                this.sizes = "text-3xl md:text-4xl xl:text-5xl";
                break;
            default:
                this.sizes = "text-base md:text-lg xl:text-xl";
        }
    }

    render() {
        return (
            <h1 className={`${this.sizes} ${this.props.className}`} style={this.props.style}>
                {this.props.children}
            </h1>
        );
    }
}
