import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import ContentArea from "../../components/ContentArea";
import Nav from "../../components/Nav";
import Header from "../../components/Header";

export default class Notfound extends Component {
    render() {
        return (
            <div className="darkgrey">
                <ContentArea>
                    <Nav id="notfoundnav" className="pt-12">
                        <div className="hidden lg:block">
                            <NavLink to="/" className="text-xl">
                                Home
                            </NavLink>
                            <NavLink to="/about" className="ml-6 text-xl">
                                About
                            </NavLink>
                            <NavLink to="/work" className="ml-6 text-xl">
                                Work
                            </NavLink>
                            <NavLink to="/contact" className="ml-6 text-xl">
                                Contact
                            </NavLink>
                        </div>
                        <div className="block lg:hidden">
                            <button id="openNav">
                                <i className="fas fa-bars text-2xl"></i>
                            </button>
                        </div>
                    </Nav>
                    <section id="notfound" className="w-full h-full flex flex-col justify-center items-center">
                        <Header size="large">404</Header>
                        <Header size="large">Not found</Header>
                    </section>
                </ContentArea>
            </div>
        );
    }
}
